@import 'gly-spin';

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}


label[required]:after {
  content: " *";
  color: red;
}

body {
  background: #FFF;
}

img {
  max-width: 100%;
}

.container h1 {
  text-align: center;
}

.item {
  width: 310px;
  display: inline-block;
  text-align: center;
  background: #f3f3f3;
}

.item img
{
  border-radius:5px;
  border:1px solid white;
  background:rgba(0,0,0,0.05);
}

.item img:hover {
  box-shadow: inset 0 0 5px 1px rgba(17, 44, 72, 0.53);
  border-radius:5px;
}


.item-container {
  text-align: center;
  vertical-align: middle;
}

.pagination-container {
  text-align: center;
}
